import { Box, Card, Grid, Stack, Typography, useTheme } from "@mui/material";
import Pill from "../ui/Pill";
import type { CaseStudySection } from "../../data/caseStudies";

const CaseStudySectionCard = ({ section }: { section: CaseStudySection }) => {
  const theme = useTheme();

  return (
    <Card
      component="section"
      id={section.id}
      variant="outlined"
      sx={{
        borderRadius: `${theme.tokens.radius.surface}px`,
        boxShadow: "none",
        px: { xs: theme.tokens.spacing.lg, md: theme.tokens.spacing.xl },
        py: { xs: theme.tokens.spacing.lg, md: theme.tokens.spacing.xl },
      }}
    >
      <Stack spacing={{ xs: theme.tokens.spacing.md, md: theme.tokens.spacing.lg }}>
        <Stack spacing={1.5}>
          <Typography variant="h3">{section.title}</Typography>
          {section.description && (
            <Typography variant="body1" color="text.secondary" sx={{ lineHeight: 1.7 }}>
              {section.description}
            </Typography>
          )}
          {section.body?.map((paragraph) => (
            <Typography
              key={paragraph.slice(0, 24)}
              variant="body1"
              color="text.secondary"
              sx={{ lineHeight: 1.7 }}
            >
              {paragraph}
            </Typography>
          ))}
        </Stack>

        {section.quotes && section.quotes.length > 0 && (
          <Stack spacing={2}>
            {section.quotes.map((quote) => (
              <Card
                key={`${quote.author}-${quote.text.slice(0, 16)}`}
                variant="outlined"
                sx={{
                  borderRadius: `${theme.tokens.radius.surface}px`,
                  boxShadow: "none",
                  backgroundColor:
                    theme.palette.mode === "light"
                      ? "rgba(34, 84, 255, 0.04)"
                      : "rgba(34, 84, 255, 0.12)",
                  px: { xs: theme.tokens.spacing.md, md: theme.tokens.spacing.lg },
                  py: { xs: theme.tokens.spacing.md, md: theme.tokens.spacing.lg },
                }}
              >
                <Stack spacing={1}>
                  <Typography variant="body1" sx={{ fontStyle: "italic", lineHeight: 1.7 }}>
                    “{quote.text}”
                  </Typography>
                  <Typography variant="caption" color="text.secondary">
                    {quote.author} — {quote.role}
                  </Typography>
                </Stack>
              </Card>
            ))}
          </Stack>
        )}

        {section.points && (
          <Stack component="ul" spacing={1} sx={{ pl: 2, mb: 0 }}>
            {section.points.map((point) => (
              <Typography component="li" key={point} variant="body1" color="text.secondary">
                {point}
              </Typography>
            ))}
          </Stack>
        )}

        {section.highlights && (
          <Stack direction="row" spacing={1} flexWrap="wrap" useFlexGap>
            {section.highlights.map((highlight) => (
              <Pill
                key={highlight}
                label={highlight}
                size="small"
                sx={{
                  backgroundColor:
                    theme.palette.mode === "light"
                      ? "rgba(34, 84, 255, 0.08)"
                      : "rgba(98, 132, 218, 0.16)",
                  color:
                    theme.palette.mode === "light"
                      ? theme.palette.brand.secondary
                      : "rgba(221, 230, 255, 0.92)",
                }}
              />
            ))}
          </Stack>
        )}

        {section.metrics && (
          <Grid container spacing={2}>
            {section.metrics.map((metric) => (
              <Grid size={{ xs: 12, md: section.metrics!.length > 1 ? 6 : 12 }} key={metric.label}>
                <Card
                  variant="outlined"
                  sx={{
                    height: "100%",
                    borderRadius: `${theme.tokens.radius.surface}px`,
                    boxShadow: "none",
                    px: { xs: theme.tokens.spacing.md, md: theme.tokens.spacing.lg },
                    py: { xs: theme.tokens.spacing.md, md: theme.tokens.spacing.lg },
                  }}
                >
                  <Stack spacing={0.75}>
                    <Typography variant="h4">{metric.value}</Typography>
                    <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                      {metric.label}
                    </Typography>
                    {metric.description && (
                      <Typography variant="body2" color="text.secondary" sx={{ lineHeight: 1.6 }}>
                        {metric.description}
                      </Typography>
                    )}
                  </Stack>
                </Card>
              </Grid>
            ))}
          </Grid>
        )}

        {section.image && (
          <Box
            component="img"
            src={section.image.src}
            alt={section.image.alt}
            sx={{
              width: "100%",
              borderRadius: `${theme.tokens.radius.surface}px`,
              boxShadow: "none",
            }}
          />
        )}
      </Stack>
    </Card>
  );
};

export default CaseStudySectionCard;

